// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";

// Material Kit 2 PRO React components
import MKBox from "components/MKBox";
import MKBadge from "components/MKBadge";
import MKTypography from "components/MKTypography";

// Material Kit 2 PRO React components
import LinkCard from "examples/Cards/BlogCards/LinkCard";

function OfficialProducts() {
  const products = [
    {
      title: "JPYC HP",
      image: "/img/externalLink/jpyc.jp.png",
      twitter: "jcam_official",
      description:
        "HPMetamaskcoin",
      action: {
        type: "external",
        route: "https://jpyc.jp/",
      },
    },
    {
      title: "JPYC Marketplace",
      image: "/img/externalLink/app.jpyc.jp.jpg",
      twitter: "jcam_official",
      description:
        "JPYCJPYCJPYCV",
      action: {
        type: "external",
        route: "https://app.jpyc.jp/",
      },
    },
    {
      title: "TipJPYC",
      image: "/img/externalLink/TipJPYC.webp",
      twitter: "tipjpyc",
      description:
        " JPYC  Twitter JPYC JPYC  Twitter ",
      action: {
        type: "external",
        route: "https://note.com/tipjpyc/n/nbc48a7a0a84e",
      },
    },
    {
      title: "JPYC Grant",
      image: "/img/externalLink/jpycgrant.png",
      twitter: "jcam_official",
      description:
        "JPYC GrantJPYCJPYC",
      action: {
        type: "external",
        route: "https://jpycgrant.studio.site/",
      },
    },
    {
      title: "Nudge | JPYC",
      image: "/img/externalLink/nudge.jpyc.png",
      twitter: "nudgecard",
      description: "JPYC",
      action: {
        type: "external",
        route: "https://nudge.cards/jpyc",
      },
    },
    {
      title: "Links JPYC Bot",
      image: "/img/externalLink/links.jpyc.bot.png",
      twitter: "LinksBTC",
      description: "SNSLinksJPYC BotBotlinks",
      action: {
        type: "external",
        route: "https://links.jpyc.jp/",
      },
    },
  ];

  const contents = products.map((item) => (
    <Grid item xs={12} md={6} lg={4}>
      <MKBox mt={3}>
        <LinkCard
          image={`${process.env.PUBLIC_URL}${item.image}`}
          title={item.title}
          description={item.description}
          action={item.action}
          twitter={item.twitter}
        />
      </MKBox>
    </Grid>
  ));

  return (
    <MKBox component="section" py={3}>
      <Container>
        <Grid
          container
          item
          flexDirection="column"
          alignItems="center"
          xs={12}
          lg={6}
          sx={{ textAlign: "center", mx: "auto" }}
        >
          <MKBadge
            variant="contained"
            color="info"
            badgeContent="Official"
            container
            circular
            sx={{ mb: 1 }}
          />
          <MKTypography variant="h2" mb={1}>
            Official Contents{" "}
          </MKTypography>
          <MKTypography variant="body2" color="text">
            JPYC
          </MKTypography>
        </Grid>
        <Grid container spacing={3} sx={{ mt: 3 }}>
          {contents}
        </Grid>
      </Container>
    </MKBox>
  );
}

export default OfficialProducts;
